<template>
  <b-field
    label="Promo Code"
    class="column is-8"
    :message="message"
    :type="validationClasses"
  >
    <form @submit.prevent="onCheck">
      <b-input v-model="code" type="text" icon="ticket" size="is-medium">
      </b-input>
    </form>
    <p class="control">
      <b-button
        @click="onCheck"
        :type="validationClasses"
        label="check code"
        :loading="isLoading"
        :disabled="code.length < 1"
        size="is-medium"
        outlined
      />
    </p>
  </b-field>
</template>

<script>
import PromoCodeService from "@/services/PromoCodeService";

export default {
  name: "PromoCodeForm",
  beforeMount() {
    const { promoCode } = this.$route.query;
    if (promoCode) {
      this.code = promoCode;
      this.onCheck();
    }
  },
  data() {
    return {
      code: "",
      codeIsValid: null,
      isLoading: false,
      message: ""
    };
  },
  methods: {
    onInput() {
      this.$emit("input", this.details);
    },
    async onCheck() {
      try {
        this.isLoading = true;
        const {
          data: { promoCode }
        } = await PromoCodeService.checkPromoCode(this.code);
        if (promoCode.isValid) {
          this.validCode(promoCode.discountPercent);
        } else {
          this.invalidCode();
        }
        console.log(promoCode);
      } catch (error) {
        this.invalidCode();
      }
      this.isLoading = false;
    },
    validCode(discountPercent) {
      this.codeIsValid = true;
      this.message = "code is valid.";
      this.$emit("valid-code", {
        code: this.code,
        discountPercent
      });
    },
    invalidCode() {
      this.codeIsValid = false;
      this.message = "code is invalid";
    }
  },
  computed: {
    validationClasses() {
      return {
        "is-success": this.codeIsValid === true,
        "is-danger": this.codeIsValid === false
      };
    }
  }
};
</script>

