<template>
  <ticket-offer-spaces-full v-if="event.soldOut" />
  <ticket-sales-closed v-else-if="!event.ticketSalesActive" />

  <div v-else>
    <page-content :contentId="contentId" :titleColumns="9" :bodyColumns="8" />

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-9">
          <form @submit.prevent="purchase()">
            <h2 class="title is-3 mx-3">Number of Tickets</h2>

            <b-field
              class="column is-5"
              label="How many tickets would you like to buy?"
            >
              <b-numberinput
                v-model="ticketCount"
                :min="1"
                :max="event.maxTicketCount"
                size="is-large"
              />
            </b-field>

            <TicketTotal
              class="mx-3"
              :count="ticketCount"
              :price="event.ticketPriceCents"
              :discountPercent="discountPercent"
            />

            <person-details-form v-model="person" :$v="$v" />

            <promo-code-form @valid-code="applyCode($event)" />

            <b-field class="column is-6">
              <b-button
                class="mt-5"
                native-type="submit"
                size="is-large"
                type="is-primary"
                >Purchase</b-button
              >
            </b-field>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import NProgress from "nprogress";
import { required, email } from "vuelidate/lib/validators";
import { mapGetters } from "vuex";

import TicketOfferSpacesFull from "@/ticketing/views/TicketOfferSpacesFull";
import TicketSalesClosed from "@/ticketing/views/TicketSalesClosed";
import PageContent from "@/ticketing/components/PageContent";
import TicketService from "@/services/TicketService";
import ProfileService from "@/services/ProfileService";

import TicketTotal from "./TicketTotal";
import PersonDetailsForm from "./PersonDetailsForm.vue";
import PromoCodeForm from "./PromoCodeForm";
import Modal from "./Modal";

const CONTENT_NAME = "direct_buy_page_content";

export default {
  name: "Purchase",
  components: {
    TicketOfferSpacesFull,
    TicketSalesClosed,
    PageContent,
    TicketTotal,
    PersonDetailsForm,
    PromoCodeForm,
    Modal
  },
  data() {
    return {
      contentName: CONTENT_NAME,
      ticketCount: 1,
      promoCode: null,
      discountPercent: 0,
      isModalActive: false,
      person: {
        firstName: "",
        email: ""
      }
    };
  },
  validations: {
    person: {
      firstName: { required },
      email: { required, email }
    }
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      content: "content/contentByField"
    }),
    contentId() {
      return this.event.pageContent.find(c => c[0] === this.contentName)[1];
    }
  },
  methods: {
    applyCode(event) {
      this.promoCode = event.code;
      this.discountPercent = event.discountPercent;
    },
    async purchase() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        const personData = {
          firstName: this.person.firstName,
          email: this.person.email
        };

        const {
          data: {
            person: { id: personId }
          }
        } = await ProfileService.createOrUpdateProfile(personData);

        const {
          data: {
            ticketOffer: { id: ticketOfferId }
          }
        } = await TicketService.createTicketOffer(this.event.slug, {
          personId,
          ticketCount: this.ticketCount,
          purchaseQuantity: this.ticketCount
        });

        const {
          data: { purchaseStatus }
        } = await ProfileService.checkPurchaseStatus(personId, this.event.id);

        if (
          purchaseStatus.hasInvitedByTicketOffer ||
          purchaseStatus.hasTicket
        ) {
          this.cardModal(purchaseStatus, personData.email);
          return;
        }
        this.checkout(ticketOfferId);
      }
    },
    async checkout(ticketOfferId) {
      NProgress.start();

      const response = await TicketService.createStripeSessionPurchaseQuantity({
        ticketOfferId,
        eventId: this.event.id,
        promoCode: this.promoCode
      });

      const publishableKey = process.env.VUE_APP_STRIPE_KEY;

      const stripe = Stripe(publishableKey);

      const stripeResult = await stripe.redirectToCheckout({
        sessionId: response.data.stripeSessionId
      });
      // If `redirectToCheckout` fails due to a browser or network
      // error, display the localized error message to your customer
      // using `stripeResult.error.message`.
    },
    cardModal(purchaseStatus, personEmail) {
      this.$buefy.modal.open({
        parent: this,
        component: Modal,
        hasModalCard: true,
        trapFocus: true,
        props: {
          purchaseStatus,
          personEmail
        }
      });
    }
  }
};
</script>

<style scoped>
.add-button {
  margin-right: 1rem;
}
</style>
